import React from 'react';
import './Contact.css';
import CommonSection from "../../components/CommonSection";
import Helmet from "../../components/Helmet";
import {socialLink} from "../../assets/data/headerTop";
import {Col, Container, Row} from "reactstrap";
import {Link} from "react-router-dom";

const Contact = () => {
    return (
        <Helmet title="">
            <CommonSection title="" />
            <section>
                <Container>
                    <Row>
                        <Col lg="6" md="6" sm="12">
                            <div className="contact__info">
                                <h6 className="fw-bold"></h6>
                                <p>
                                       <br/>
                                    220035 . , ., 4-282
                                </p>
                                <div className="mt-2">
                                    <h6 className="fs-6 mb-0">:</h6>
                                    <p>
                                        +375 29 1 316 316<br/>
                                        +375 44 5 682 838
                                    </p>
                                </div>

                                <div className="mt-2">
                                    <h6 className="mb-0 fs-6">Email:</h6>
                                    <p>info@around.by</p>
                                </div>
                            </div>
                        </Col>
                        <Col lg="6" md="6" sm="12">
                            <div className="contact__info">
                                <h6 className="fw-bold"></h6>
                                <p>
                                     BY14PJCB30130676640000000933 <br/>
                                     BYN    <br/>
                                    : PJCBBY2X<br/>
                                    : 193283318
                                </p>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg='12'>
                            <div className="contact__info">
                                <h6 className="fw-bold mt-4">  </h6>

                                <div className=" d-flex align-items-center gap-4 mt-3">
                                    {socialLink.map(({url, icon}) => (
                                        <Link
                                            to={url}
                                            key={icon}
                                            className="social__link-icon"
                                        >
                                            <i className={icon}></i>
                                        </Link>
                                    ))}
                                </div>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        </Helmet>
    );
};

export default Contact;
